.layout-page-hot-deal{
    .text-danger{
        color: #c73030 !important;
    }
}

.layout-nav{
    a{
        padding: 15px;
        text-decoration: none;
        border-bottom: 2px solid transparent;
        &.text-danger{
            border-color: #c73030;
        }
    }
}

.layout-sort{
    gap: 10px;
    font-size: 14px;
    padding-right: 15px;
    select{
        cursor: pointer;
        border: none;
        outline: none;
    }
}

.layout-sale-categories{
    gap: 10px;
    a{
        color: #707070;
        padding: 6px 10px;
        border-radius: 5px;
    }
}

.layout-sale-main{
    grid-template-columns: repeat(5, 1fr);
    grid-gap: 0 10px;
    padding: 5px;
}
